import React, { useEffect, useState } from "react";
import { Container, Row, Col, Card, Form, Button } from "react-bootstrap";
import { AiOutlineMail, AiFillGithub, AiOutlineEnvironment } from "react-icons/ai";
import Particle from "../Particle";
import ReactGA from "react-ga";

const contactEmail = "aminelahlou007@gmail.com";
const githubLink = "https://github.com/aminelahlou007-debug";
const locationLabel = "Maroc";

function Contact() {
  const [formData, setFormData] = useState({ name: "", email: "", message: "" });
  const [status, setStatus] = useState("");

  useEffect(() => {
    ReactGA.pageview(window.location.pathname + window.location.search);
  }, []);

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    const { name, email, message } = formData;

    if (!name || !email || !message) {
      setStatus("Merci de remplir tous les champs.");
      return;
    }

    const subject = encodeURIComponent("Nouveau message - Portfolio");
    const body = encodeURIComponent(`Nom: ${name}\nEmail: ${email}\n\n${message}`);
    window.location.href = `mailto:${contactEmail}?subject=${subject}&body=${body}`;
    setStatus("Merci ! Votre client mail s'ouvrira pour finaliser l'envoi.");
  };

  return (
    <Container fluid className="contact-section" id="contact">
      <Particle />
      <Container>
        <Row className="justify-content-center" style={{ paddingBottom: "30px" }}>
          <Col md={12} className="text-center">
            <div className="hero-title-separator" style={{ justifyContent: "center", marginBottom: "20px" }}>
              <span className="hero-line"></span>
              <span className="hero-title">Contact</span>
              <span className="hero-diamond">✦</span>
            </div>
            <h1 className="contact-heading" style={{ marginTop: "15px" }}>
              Restons <strong className="purple">en contact</strong>
            </h1>
            <p className="contact-subtitle">
              Une opportunité, une question ou juste envie d&apos;échanger ? Je réponds rapidement.
            </p>
          </Col>
        </Row>

        <Row>
          <Col md={7} className="mb-4">
            <Card className="contact-card">
              <Card.Body>
                <Card.Title className="contact-card-title">Envoyez-moi un message</Card.Title>
                <Form className="contact-form" onSubmit={handleSubmit}>
                  <Form.Group controlId="contactName" className="mb-3">
                    <Form.Label>Nom</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Votre nom"
                      name="name"
                      value={formData.name}
                      onChange={handleChange}
                    />
                  </Form.Group>

                  <Form.Group controlId="contactEmail" className="mb-3">
                    <Form.Label>Email</Form.Label>
                    <Form.Control
                      type="email"
                      placeholder="votre@email.com"
                      name="email"
                      value={formData.email}
                      onChange={handleChange}
                    />
                  </Form.Group>

                  <Form.Group controlId="contactMessage" className="mb-4">
                    <Form.Label>Message</Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={5}
                      placeholder="Décrivez brièvement votre projet ou question."
                      name="message"
                      value={formData.message}
                      onChange={handleChange}
                    />
                  </Form.Group>

                  <div className="d-flex align-items-center" style={{ gap: "12px" }}>
                    <Button variant="primary" type="submit">
                      Envoyer
                    </Button>
                    {status && <span className="contact-status">{status}</span>}
                  </div>
                </Form>
              </Card.Body>
            </Card>
          </Col>

          <Col md={5} className="mb-4">
            <Card className="contact-card">
              <Card.Body>
                <Card.Title className="contact-card-title">Coordonnées directes</Card.Title>
                <div className="contact-links">
                  <div className="contact-link">
                    <span className="contact-icon">
                      <AiOutlineMail />
                    </span>
                    <div>
                      <div className="contact-label">Email</div>
                      <a className="contact-anchor" href={`mailto:${contactEmail}`}>
                        {contactEmail}
                      </a>
                    </div>
                  </div>

                  <div className="contact-link">
                    <span className="contact-icon">
                      <AiFillGithub />
                    </span>
                    <div>
                      <div className="contact-label">GitHub</div>
                      <a
                        className="contact-anchor"
                        href={githubLink}
                        target="_blank"
                        rel="noreferrer"
                      >
                        {githubLink.replace("https://", "")}
                      </a>
                    </div>
                  </div>

                  <div className="contact-link">
                    <span className="contact-icon">
                      <AiOutlineEnvironment />
                    </span>
                    <div>
                      <div className="contact-label">Localisation</div>
                      <p className="mb-0 contact-anchor">{locationLabel}</p>
                    </div>
                  </div>
                </div>

                <div className="contact-note">
                  Disponible pour des missions freelance et des opportunités à distance.
                </div>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </Container>
  );
}

export default Contact;
